
@import './colors'; 
@import '/src/assets/01_style/01_layout/layoutType01.scss';


.App{
	background-color: $colorBody;
  color: $colorTxt;
  height: 100vh;
  width: 100vw;

}


.container {

  .header {

  }

  .content {
    .content1 {

    }

    .content2 {

    }
  }

  /*
  @media (max-width: 768px)
  |-------------------------| 
  |           header        |
  |-------------------------|
  |        (content)        |
  |         content1        |  
  |-------------------------|         
  |         content2        |
  |                         |
  |-------------------------|
  */
	@media (max-width: 768px) {
		.content {
			flex-direction: column; /* nav를 아래로 이동 */
	
			.content1 {
				width: 100%;
				height: 5%; /* 높이 설정 */
			}
	
			.content2 {
				width: 100%; 
				order: 2; /* main-content를 nav 아래로 */
				flex: 1;
			}
		}
	}

}


