// OnyoyProfile.module.scss

$box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);

.container {
  display: grid;
  grid-template-columns: auto auto; // 왼쪽 소개, 오른쪽 이미지 갤러리
	text-align: left;
  font-family: Arial, sans-serif;
  color: #333;
  background-color: #f4f4f4;
  padding: 40px;
}

.profileContainer {
  display: flex;
	position: relative;
  flex-direction: column;
  align-items: center;
	text-align: left;
  font-family: Arial, sans-serif;
  color: #333;
  background-color: #f4f4f4;
	
  padding: 40px;
}

.profileContent {
  display: flex;
  gap: 30px;
  max-width: 1000px;
  margin-bottom: 40px;
}

.profileImage {
  width: 300px;
  height: auto;
  border-radius: 12px;
  object-fit: cover;
}

.profileDescription {
  background-color: #fff;
  padding: 30px;
  border-radius: 12px;
  box-shadow: $box-shadow;
  flex: 1;

  h2 {
    text-align: center;
    font-weight: bold;
    font-size: 2rem;
    margin-bottom: 10px;
    span {
      font-weight: bold;
    }
  }

  p {
    line-height: 1.8;
    font-size: 1.1rem;
  }
}

.profileCard {
	position: relative; 
  background-color: #fff;
  padding: 30px;
  border-radius: 12px;
  box-shadow: $box-shadow;
  max-width: 1000px;
  width: 100%;
  margin-bottom: 30px;

  h3 {
    margin-bottom: 15px;
    font-size: 1.5rem;
  }

  ul {
    padding-left: 30px;
    line-height: 1.8;
    font-size: 1.1rem;
  }
}

.careerContent {
  width: 85%;
}


.imageGallery {
  position: relative; // 이미지를 오른쪽에 배치
  top: 40px; // 프로필 콘텐츠와 수직 간격 조정
	align-items: center;
  display: flex;
  flex-direction: column;
	padding: 20px;
  gap: 50px;
}

.galleryImage {
  width: 50%;
  height: auto;
  border-radius: 8px;
  box-shadow: $box-shadow;
  object-fit: cover;
}

.galleryImage:nth-child(odd) {
  align-self: flex-end; // 홀수 번째 이미지는 오른쪽 정렬
	transform: translateX(-20%);
}

.galleryImage:nth-child(even) {
  align-self: flex-start; // 짝수 번째 이미지는 왼쪽 정렬
	transform: translateX(30%);
	transform: translateY(-40%);
}