
    /*
  |-------------------------| 
  |           header        |
  |-------------------------|
  |        (content)        |
  |         content1        |  
  |-------------------------|         
  |         content2        |
  |                         |
  |-------------------------|
    */
    
.container {
  display: flex;
  flex-direction: column;
  .header {
    height: 8%;
  }

  .content {
    flex-direction: column; /* nav를 아래로 이동 */
    .content1 {
      width: 100%;
      height: 5%; /* 높이 설정 */
    }

    .content2 {
      width: 100%; 
      order: 2; /* main-content를 nav 아래로 */
      flex: 1;
    }
  }
}


